.normal-button {
  display: inline-block;
  border: none;
  padding: 1rem 2rem;
  margin: 0;
  text-decoration: none;
  background: black;
  color: #ffffff;
  font-size: 1rem;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, 
              transform 150ms ease;
}

.normal-button:hover {
  background: grey;
}

.normal-button:focus {
  outline: none;
}

.normal-button:active {
  transform: scale(1.5);
}