.watch-time-text {
  font-size: 100px;
  color: black
}

.watch-status-text {
  font-size: 20px;
}


.heart-icon {
  width: 20%;
  height: 20%;
}

.heart-icon-animated {
  animation: heartbeat 1s infinite;
}

.heart-icon-animated img {
  color: red;
  background-color: red;
}

@keyframes heartbeat
{
  0%
  {
    transform: scale( .75 );
  }
  20%
  {
    transform: scale( 1 );
  }
  40%
  {
    transform: scale( .75 );
  }
  60%
  {
    transform: scale( 1 );
  }
  80%
  {
    transform: scale( .75 );
  }
  100%
  {
    transform: scale( .75 );
  }
}